<template>
  <div class="content-siteInfo">
    <headerUsually titleText="工作站资料" btnShow="false" url></headerUsually>
    <div class="content-main">
      <div class="item" v-for="(item,index) in siteInfo" :key="index">
        <div class="text">{{item.text}}</div>
        <div class="val" v-if="item.isEdit==false">{{item.value}}</div>
        <input class="val" v-else v-model="item.value" :placeholder="item.placeholder">
      </div>
    </div>
    <div class="btn" @click="okFun()">确定</div>
  </div>
</template>

<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      siteInfo: [
        {
          text: "描述",
          value: "",
          placeholder: "请输入站点描述",
          isEdit: true
        },
        { text: "姓名", value: "", placeholder: "请输入姓名", isEdit: true },
        { text: "电话", value: "", placeholder: "请输入电话", isEdit: true }
      ]
    };
  },
  methods: {
    async okFun() {
      let isPhone = /^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/;
      let isMob = /^(?:13\d|15\d|18[123456789])-?\d{5}(\d{3}|\*{3})$/;
      if (
        !(
          isPhone.test(this.siteInfo[2].value) ||
          isMob.test(this.siteInfo[2].value)
        )
      ) {
        this.msg("电话号码格式不正确！");
        return;
      }
      let pd = this.siteInfo.every(item => {
        return item.value;
      });

      if (pd) {
        let dcgateway = localStorage.getItem("dcgateway")
          ? JSON.parse(localStorage.getItem("dcgateway"))
          : {};
        let siteInfos = {
          stationDesc: this.siteInfo[0].value,
          magicCode: dcgateway.userInfo.magicNo,
          principal: this.siteInfo[1].value,
          phone: this.siteInfo[2].value
        };
        let data = {
          params: {
            ...siteInfos
          }
        };
        let res = await ajaxGet("/pos/router?method=saveWorkStation", data);
        if (res && res.resultCode == 0) {
          dcgateway.siteInfo = {
            ...siteInfos
          };
          dcgateway.userInfo.workStationCode = res.data.workStationCode;
          localStorage.setItem("dcgateway", JSON.stringify(dcgateway));
          this.$router.push("/mobileOrdering/selectClass");
        }
      } else {
        this.msg("描述/姓名 不能为空！");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  }
};
</script>

<style lang="less" scoped>
.content-siteInfo {
  padding-top: 46px;
  .content-main {
    background-color: #fff;
    .item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 14px;
      margin-left: 15px;
      height: 54px;
      padding: 0;
      padding-right: 15px;
      border-bottom: 1px solid #ddd;
      box-sizing: border-box;
      .val {
        text-align: right;
      }
      input {
        outline: none;
        border: none;
      }
    }
    .item:last-child {
      border-bottom: none;
    }
  }
  .btn {
    font-size: 14px;
    margin-top: 20px;
    padding: 11px;
    text-align: center;
    background-color: #fff;
    color: #c4ad68;
  }
}
</style>



